import React, { useState } from 'react';
import Table from '@cloudscape-design/components/table';
import Box from '@cloudscape-design/components/box';
import SpaceBetween from '@cloudscape-design/components/space-between';
import Button from '@cloudscape-design/components/button';
import Header from '@cloudscape-design/components/header';
import Pagination from '@cloudscape-design/components/pagination';
import TextFilter from '@cloudscape-design/components/text-filter';

// Sample data for the table
const SAMPLE_DATA = [
  {
    id: '1',
    name: 'Database Instance 1',
    type: 'db.t3.medium',
    status: 'Running',
    region: 'us-east-1',
    cost: '$45.60'
  },
  {
    id: '2',
    name: 'Database Instance 2',
    type: 'db.t3.large',
    status: 'Running',
    region: 'us-west-2',
    cost: '$91.20'
  },
  {
    id: '3',
    name: 'Database Instance 3',
    type: 'db.t3.small',
    status: 'Stopped',
    region: 'eu-west-1',
    cost: '$0.00'
  },
  {
    id: '4',
    name: 'Database Instance 4',
    type: 'db.r5.xlarge',
    status: 'Running',
    region: 'ap-southeast-1',
    cost: '$182.40'
  },
  {
    id: '5',
    name: 'Database Instance 5',
    type: 'db.t3.micro',
    status: 'Running',
    region: 'us-east-1',
    cost: '$15.20'
  },
];

export default function TableDemo() {
  const [selectedItems, setSelectedItems] = useState([]);
  const [filteringText, setFilteringText] = useState('');
  const [currentPageIndex, setCurrentPageIndex] = useState(1);

  // Filter items based on search text
  const filteredItems = SAMPLE_DATA.filter(item =>
    item.name.toLowerCase().includes(filteringText.toLowerCase()) ||
    item.type.toLowerCase().includes(filteringText.toLowerCase()) ||
    item.region.toLowerCase().includes(filteringText.toLowerCase())
  );

  return (
    <Table
      columnDefinitions={[
        {
          id: 'name',
          header: 'Instance Name',
          cell: item => item.name,
          sortingField: 'name',
          isRowHeader: true
        },
        {
          id: 'type',
          header: 'Instance Type',
          cell: item => item.type,
          sortingField: 'type'
        },
        {
          id: 'status',
          header: 'Status',
          cell: item => (
            <Box
              color={item.status === 'Running' ? 'text-status-success' : 'text-status-inactive'}
            >
              {item.status}
            </Box>
          ),
          sortingField: 'status'
        },
        {
          id: 'region',
          header: 'Region',
          cell: item => item.region,
          sortingField: 'region'
        },
        {
          id: 'cost',
          header: 'Monthly Cost',
          cell: item => item.cost,
          sortingField: 'cost'
        }
      ]}
      items={filteredItems}
      selectionType="multi"
      selectedItems={selectedItems}
      onSelectionChange={({ detail }) => setSelectedItems(detail.selectedItems)}
      header={
        <Header
          counter={`(${filteredItems.length})`}
          actions={
            <SpaceBetween direction="horizontal" size="xs">
              <Button>View details</Button>
              <Button variant="primary">Create instance</Button>
            </SpaceBetween>
          }
        >
          Database Instances
        </Header>
      }
      filter={
        <TextFilter
          filteringText={filteringText}
          filteringPlaceholder="Find instances"
          onChange={({ detail }) => setFilteringText(detail.filteringText)}
          countText={`${filteredItems.length} ${filteredItems.length === 1 ? 'match' : 'matches'}`}
        />
      }
      pagination={
        <Pagination
          currentPageIndex={currentPageIndex}
          onChange={({ detail }) => setCurrentPageIndex(detail.currentPageIndex)}
          pagesCount={Math.ceil(filteredItems.length / 5)}
        />
      }
      empty={
        <Box textAlign="center" color="inherit">
          <b>No instances</b>
          <Box padding={{ bottom: 's' }} variant="p" color="inherit">
            No instances to display.
          </Box>
          <Button>Create instance</Button>
        </Box>
      }
    />
  );
}
